<template>
  <div class="turkey-syria-earthquake-view">
    <div class="header">
      <h1>Turkey-Syria Earthquake Analysis</h1>
      <p class="subtitle">Interactive analysis and visualization of the Turkey-Syria earthquake disaster</p>
    </div>
    
    <div class="navigation">
      <button 
        v-for="category in categories" 
        :key="category"
        @click="selectedCategory = category"
        :class="['nav-btn', { active: selectedCategory === category }]"
      >
        {{ category }}
      </button>
    </div>

    <div class="pages-container">
      <div v-for="page in filteredPages" :key="page.filename" class="page-card">
        <h3>{{ page.title }}</h3>
        <p class="category">{{ page.category }}</p>
        <button @click="loadPage(page.filename)" class="view-btn">View Analysis</button>
      </div>
    </div>
    
    <div v-if="selectedPageContent" class="page-content">
      <button @click="closeContent" class="close-btn">Ã—</button>
      <div v-html="selectedPageContent" class="content"></div>
    </div>
  </div>
</template>

<script>
import PagesService from '@/services/pagesService'

export default {
  name: 'BookTurkeySyriaEarthquake',
  data() {
    return {
      allPages: [],
      selectedCategory: 'All',
      selectedPageContent: null,
      categories: ['All']
    }
  },
  computed: {
    filteredPages() {
      if (this.selectedCategory === 'All') {
        return this.allPages
      }
      return this.allPages.filter(page => page.category === this.selectedCategory)
    }
  },
  async mounted() {
    this.allPages = await PagesService.getAllPages()
    
    // Extract unique categories
    const uniqueCategories = [...new Set(this.allPages.map(page => page.category))]
    this.categories = ['All', ...uniqueCategories]
    
    // Default to Turkey-Syria earthquake category if available
    const earthquakeCategory = uniqueCategories.find(cat => 
      cat.toLowerCase().includes('turkey') || cat.toLowerCase().includes('earthquake')
    )
    if (earthquakeCategory) {
      this.selectedCategory = earthquakeCategory
    }
  },
  methods: {
    async loadPage(filename) {
      try {
        const content = await PagesService.loadPageContent(filename)
        this.selectedPageContent = PagesService.markdownToHtml(content)
      } catch (error) {
        console.error('Error loading page:', error)
        this.selectedPageContent = '<p>Error loading page content. Please try again.</p>'
      }
    },
    closeContent() {
      this.selectedPageContent = null
    }
  }
}
</script>

